import Home from './pages2/home.vue';
import Discovery from './pages2/discovery.vue';
import Me from './pages2/me.vue';
import Messages from './pages2/messages.vue';
import Nodes from './pages2/nodes.vue';
import Node from './pages2/go.vue';
import AllNode from './pages2/allNodes.vue';
import Member from './pages2/member.vue';
import MemberTopic from './pages2/memberTopic.vue';
import MemberReply from './pages2/memberReply.vue';
import ChatDetail from './pages2/chatDetail.vue';

import NotFound from './pages2/404.vue';

// Pages
export default [
  {
    path: '/',
    component: Home,
  },
  {
    path: '/nodes/',
    component: Nodes,
  },
  {
    path: '/all-nodes/',
    component: AllNode,
  },
  {
    path: '/discovery/',
    component: Discovery,
  },
  {
    path: '/me/',
    component: Me,
  },
  {
    path: '/t/:id',
    component: Messages,
  },
  {
    path: '/chatDetail/:chatInfo',
    component: ChatDetail,
  },
  {
    path: '/go/:name',
    component: Node,
  },
  {
    path: '/member/:name',
    component: Member,
  },
  {
    path: '/member/:name/topics',
    component: MemberTopic,
  },
  {
    path: '/member/:name/replies',
    component: MemberReply,
  },
  // Default route (404 page). MUST BE THE LAST
  {
    path: '(.*)',
    component: NotFound,
  },
];
